* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --black: #111;
  --black_1: #202024;
  --grey: #b4afaf;
  --lighygrey: #e0e0e0;
  --violet: #4f2ee8;
  --white: #fff;
  --font-size-extrabig: 48px;
  --font-size-big: 32px;
  --font-size-normal: 22px;
  --font-size-normal_1: 18px;
  --font-size-small: 16px;
  --fint-size-small_1: 14px;
  --font-size-small_2: 12px;
}

img {
  border-radius: 8px;
}

h3 {
  color: #111;
  letter-spacing: -4%;
  padding: 14px 0 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

p {
  color: #b4afaf;
  letter-spacing: -4%;
  padding-bottom: 20px;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
}

body {
  margin: 0 auto;
  padding: 0;
}

.is_hidden {
  visibility: hidden;
}

.icon_light, .lock-icon {
  display: block;
}

.modal_signup-mobile, .modal_input-icon-pass-white, .modal_input-icon-pass-white, .img_dark-close, .hamburger_open-svg-white {
  display: none;
}

.backdrop {
  visibility: hidden;
  z-index: 2;
  background-color: #6a52be;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.dark-mode {
  color: #fff;
  background-color: #333;
}

.logo_dark, .dark_mode, .btn_log-out-desktop, .btn_log-out-mobile, .hamburger_open, .hamburger_close {
  display: none;
}

.header_wrapper {
  background-color: "#f6f6f6";
  border: 1.5px solid #000;
  border-radius: 0 0 8px 8px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 72px;
  display: flex;
  position: relative;
}

.logo {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  left: 2%;
}

.home_shopping_list {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  margin-left: 76px;
  display: flex;
  position: absolute;
  left: 20%;
}

.shoping-list_text {
  z-index: 5;
  justify-content: center;
  align-items: center;
  margin-left: 40px;
  display: flex;
  position: relative;
}

.shoping-list_text:hover:after {
  content: "";
  z-index: -1;
  background-color: #eac645;
  border-radius: 150px;
  width: 180px;
  height: 45px;
  position: absolute;
  bottom: 3px;
  left: -15px;
}

.home_text {
  z-index: 5;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.home_text:hover:after {
  content: "";
  z-index: -1;
  background-color: #eac645;
  border-radius: 150px;
  width: 70px;
  height: 45px;
  position: absolute;
  top: -13px;
  left: -10px;
}

.lock-icon {
  margin-left: 5%;
}

.switcher_button {
  justify-content: center;
  align-items: center;
  margin-right: 24px;
  display: flex;
  position: absolute;
  right: 15%;
}

#themeSwitch {
  opacity: 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.button_login {
  background-color: "#f6f6f6";
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  right: 24px;
}

.btn_log-in {
  cursor: pointer;
  border: none;
}

.lock-icon {
  fill: #fff;
}

.modal_signup {
  z-index: 5;
  background-color: #f6f6f6;
  border: 2px solid #000;
  border-radius: 18px;
  width: 579px;
  height: 516px;
  position: absolute;
  top: 100%;
}

.modal_signup-form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  margin-top: 15%;
  display: flex;
  position: relative;
}

.modal_signup-field-name, .modal_signup-field-email, .modal_signup-field-pass {
  background-color: #f6f6f6;
  border-radius: 150px;
  width: 499px;
  height: 50px;
  display: flex;
}

.modal_signup-name, .modal_signup-email, .modal_signup-password {
  margin-top: 2%;
}

.modal_input-name, .modal_input-mail {
  background-color: #f6f6f6;
  border: none;
  justify-content: center;
  align-items: center;
  width: 370px;
  margin-top: 2%;
  display: flex;
}

.modal_input-pass {
  background-color: #f6f6f6;
  border: none;
  justify-content: center;
  align-items: center;
  width: 325px;
  margin-top: 2%;
  display: flex;
}

.modal_singup_close {
  cursor: pointer;
  background-color: #f6f6f6;
  border: none;
  position: absolute;
  top: 24px;
  right: 24px;
}

.modal_btn {
  color: #f6f6f6;
  cursor: pointer;
  background-color: #000;
  border-radius: 150px;
  width: 499px;
  height: 64px;
}

.modal_list {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-right: 50px;
  list-style-type: none;
  display: flex;
}

.modal_link {
  color: gray;
  text-decoration: none;
}

.modal_link:hover {
  color: #19043a;
  text-decoration-line: underline;
}

@media screen and (width <= 768px) and (orientation: portrait) {
  .is_hidden {
    visibility: hidden;
  }

  .header_wrapper {
    background-color: "#f6f6f6";
    border: 1.5px solid #000;
    border-radius: 0 0 8px 8px;
    justify-content: center;
    align-items: center;
    max-width: 100vw;
    height: 69px;
    display: flex;
    position: relative;
  }

  .home_shopping_list, .button_login {
    display: none;
  }

  .hamburger_open {
    border: none;
    display: block;
    position: absolute;
    top: 10px;
    right: 6px;
  }

  #themeSwitch {
    margin-top: .2em;
    margin-right: 3em;
  }

  .hamburger_close {
    border: none;
    display: none;
    position: absolute;
    top: 10px;
    right: 6px;
  }

  .btn_log-out-mobile {
    display: block;
  }

  .modal_mobile {
    opacity: 0;
    background: linear-gradient(#4f2ee8 35%, #fff 100%);
    border: 1px solid #000;
    flex-direction: column;
    width: 100vw;
    height: 812px;
    display: flex;
    position: absolute;
    top: 70px;
  }

  .img_css-mobile {
    background-image: url("modal-mobile-books.png.f864b721.png");
    background-repeat: no-repeat;
    background-size: cover;
    height: 439px;
    margin-top: 115%;
  }

  .btn_log-out-mobile {
    background-color: #4f2ee8;
    border: none;
    width: 184px;
    margin-top: 10%;
    margin-left: 5%;
  }

  .modal_signup-mobile {
    z-index: 5;
    background-color: #333;
    border-radius: 18px;
    width: 100vw;
    height: 384px;
    display: none;
    position: absolute;
    top: 100%;
  }

  .modal_signup-form-mobile {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 14px;
    margin-top: 15%;
    display: flex;
    position: relative;
  }

  .modal_signup-field-name-mobile, .modal_signup-field-email-mobile, .modal_signup-field-pass-mobile {
    background-color: #333;
    border-radius: 150px;
    width: 295px;
    height: 50px;
    display: flex;
  }

  .modal_signup-name-mobile, .modal_signup-email-mobile, .modal_signup-password-mobile {
    margin-top: 4%;
  }

  .modal_input-name-mobile {
    background-color: #333;
    border: none;
    justify-content: center;
    align-items: center;
    width: 200px;
    margin-top: 2%;
    margin-left: 2%;
    display: flex;
  }

  .modal_input-mail-mobile {
    background-color: #333;
    border: none;
    justify-content: center;
    align-items: center;
    width: 185px;
    margin-top: 2%;
    margin-left: 2%;
    display: flex;
  }

  .modal_input-pass-mobile {
    background-color: #333;
    border: none;
    justify-content: center;
    align-items: center;
    width: 140px;
    margin-top: 2%;
    margin-left: 2%;
    display: flex;
  }

  .modal_btn-mobile {
    color: #333;
    cursor: pointer;
    background-color: #000;
    border-radius: 150px;
    width: 499px;
    height: 64px;
  }

  .modal_list-mobile {
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-right: 50px;
    list-style-type: none;
    display: flex;
  }

  .modal_link-mobile {
    color: gray;
    text-decoration: none;
  }

  .modal_singup_close-mobile {
    cursor: pointer;
    background-color: #333;
    border: none;
    position: absolute;
    top: 12px;
    right: 12px;
  }

  .modal_btn-submit-mobile {
    color: #000;
    cursor: pointer;
    background-color: #fff;
    border-radius: 150px;
    width: 295px;
    height: 50px;
  }
}

body {
  background-color: #f2f2f2;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.home {
  background-color: #fff;
  border: 1px solid #ddd;
  max-width: 1440px;
  margin: 0 auto;
  padding: 20px;
}

@media screen and (width >= 1200px) {
