    display: flex;
  }

  .book-list-block {
    padding-left: 40px;
  }
}

.left-section {
  width: 335px;
  height: 628px;
}

.categories-title {
  color: #4f2ee8;
  letter-spacing: 2%;
  font-size: 18px;
  font-weight: 700;
}

